import React, { useState, useEffect } from 'react';

import personsService from './services/persons';

import Persons from './Persons';
import Filter from './Filter';
import PersonForm from './PersonForm';
import Notification from './Notification';

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState('');
  const [newNumber, setNewNumber] = useState('');
  const [filter, setFilter] = useState('');
  const [notification, setNotification] = useState(null);

  const handleNameChange = (event) => {
    setNewName(event.target.value);
  };

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value);
  };

  const handleFilterChange = (event) => {
    setFilter(event.target.value.toLowerCase());
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const person = persons.find(
      (person) => person.name.toLowerCase() === newName.toLowerCase()
    );
    if (person) {
      if (newNumber !== person.number) {
        const replace = window.confirm(
          `${person.name} is already added to the phonebook, do you want to replace the old phone number with the new one?`
        );

        if (replace) {
          return personsService
            .changeNumber({ ...person, number: newNumber })
            .then(() => {
              setPersons((previous) =>
                previous.map((prevPerson) => {
                  if (prevPerson.id === person.id) {
                    prevPerson.number = newNumber;
                  }
                  return prevPerson;
                })
              );
              setNotification({
                message: `Updated phone number`,
                success: true,
              });
            })
            .catch((err) =>
              setNotification({ message: err.message, success: false })
            );
        }
      }
      return alert('Person is already in phonebook');
    }
    personsService
      .addPerson({ name: newName, number: newNumber })
      .then((response) => {
        setPersons((previous) => [
          ...previous,
          { name: newName, number: newNumber, id: response.id },
        ]);
        setNotification({ message: `Added ${newName}`, success: true });
        setNewNumber('');
        setNewName('');
      })
      .catch((err) =>
        setNotification({
          message: err.message,
          sucess: false,
        })
      );
  };

  const handleDelete = (person) => {
    const result = window.confirm(`Delete ${person.name}?`);
    if (result) {
      personsService
        .deletePerson(person.id)
        .then(() => {
          setPersons((previous) =>
            previous.filter((prevPerson) => prevPerson.id !== person.id)
          );
          setNotification({ message: 'Deleted Successfully', success: true });
        })
        .catch((err) =>
          setNotification({ message: err.message, success: false })
        );
    }
  };

  useEffect(() => {
    personsService.getPersons().then((persons) => {
      setPersons(persons);
    });
  }, []);

  return (
    <div>
      {notification && (
        <Notification
          success={notification.success}
          hide={() => setNotification(null)}
        >
          {notification.message}
        </Notification>
      )}
      <h2>Phonebook</h2>
      <Filter onFilterChange={handleFilterChange} />
      <h2>Add new</h2>
      <PersonForm
        onSubmit={handleSubmit}
        onNameChange={handleNameChange}
        onNumberChange={handleNumberChange}
        newName={newName}
        newNumber={newNumber}
      />
      <h2>Numbers</h2>
      <Persons persons={persons} filter={filter} onDelete={handleDelete} />
    </div>
  );
};

export default App;
