import React, { Fragment, useEffect } from 'react';
import './notification.css';

const Notification = ({ children, success, hide }) => {
  useEffect(() => {
    setTimeout(() => {
      hide();
    }, 5000);
  }, [hide]);

  return (
    <Fragment>
      {!children ? null : (
        <div
          className="notification"
          style={{ backgroundColor: success ? 'green' : 'red' }}
        >
          {children}
        </div>
      )}
    </Fragment>
  );
};

export default Notification;
